<template>
	<view class="materials">
		<view class="echarts-wrap">
			<mpvue-echarts class="ec-canvas" @onInit="onInit" canvasId="demo-canvas" ref="chart1" />
		</view>
		<button @click="changeChart">更改</button>
	</view>
</template>

<script>
	import * as echarts from "echarts/dist/echarts.min";
	import mpvueEcharts from "@/components/mpvue-echarts/src/echarts.vue";

	function getDate(date) {
		return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
	}
	let chart1 = null;
	export default {
		components: {
			mpvueEcharts
		},
		data() {
			return {};
		},
		onReady() {},
		methods: {
			changeChart() {
				chart1.setOption(this.getOptions(10, 30));
			},
			getOptions(nan, nv) {
				return {
					title: {
						text: "性别比例",
						x: "center",
						textStyle: {
							fontSize: 16
						}
					},
					backgroundColor: "#FFF",
					tooltip: {
						trigger: "axis",
						axisPointer: {
							// 坐标轴指示器，坐标轴触发有效  
							type: "shadow" // 默认为直线，可选为：'line' | 'shadow'  
						}
					},
					xAxis: {
						type: "value",
						splitNumber: 7
					},
					yAxis: {
						type: "category",
						show: false,
						data: [getDate(new Date())]
					},
					series: [{
							name: "男",
							type: "bar",
							stack: "总量",
							data: [nan],
							barWidth: 50,
							itemStyle: {
								normal: {
									color: "#00aaff"
								}
							}
						},
						{
							name: "女",
							type: "bar",
							stack: "总量",
							data: [nv],
							itemStyle: {
								normal: {
									color: "#f4516c"
								}
							}
						}
					]
				};
			},
			onInit(e) {
				let {
					width,
					height
				} = e;
				let canvas = this.$refs.chart1.canvas;
				echarts.setCanvasCreator(() => canvas);
				chart1 = echarts.init(canvas, null, {
					width: width,
					height: height
				});
				canvas.setChart(chart1);
				chart1.setOption(this.getOptions(50, 10));
				this.$refs.chart1.setChart(chart1);
			}
		}
	};
</script>
<style scoped lang="scss">
	.echarts-wrap {
		width: 100%;
		height: 300px;
	}
</style>
